	module.exports = {"data":{"wordpressPost":{"title":"#BMJOpen5years: 2012 article highlight","content":"<h1 id=\"article-title-1\">Hypnotics&#8217; association with mortality</h1>\n<p><em>This week we&#8217;re celebrating our fifth anniversary by highlighting some of the articles that have gained us attention, along with some that demonstrate our unique approach. </em></p>\n<p>Cited 66 times in Web of Science and with an Altmetric score of 480, &#8220;<a href=\"http://bmjopen.bmj.com/content/2/1/e000850.full\">Hypnotics&#8217; association with mortality or cancer: a matched cohort study</a>&#8221; is certainly one of the most influential papers ever published in <em>BMJ Open</em>. Using a database including more than 10,000 patients, the study from Daniel Kripke and colleagues highlighted an association between sleeping pills and mortality.</p>\n<p><a href=\"https://blogs.bmj.com/bmjopen/files/2016/02/hypnotics.png\" rel=\"attachment wp-att-962\"><img class=\"alignnone size-medium wp-image-962\" src=\"https://blogs.bmj.com/bmjopen/files/2016/02/hypnotics-300x207.png\" alt=\"hypnotics\" width=\"300\" height=\"207\" srcset=\"https://blogs.bmj.com/bmjopen/files/2016/02/hypnotics-300x207.png 300w, https://blogs.bmj.com/bmjopen/files/2016/02/hypnotics.png 513w\" sizes=\"(max-width: 300px) 100vw, 300px\" /></a></p>\n<p>We wrote to Dr Kripke to comment on the impact his article has had around the world:</p>\n<p><em>&#8220;Publishing in BMJ Open was very satisfying, since the review was fast and fair, editing done well, and publication rapid and accessible.  The press release was remarkably effective.  In 50 years of research, I have never had an article covered so widely:  not only in European and North American media, but also in Japan, China, Thailand, even Myanmar, Bali, Yemen, and Ruanda.</em></p>\n<p><em>&#8220;The important result, four years later, is at least 50 new studies of hypnotics risks, most of which mention our work.  This growing scientific focus is confirming and defining the risks of hypnotics more clearly.  I was particularly happy with <a href=\"http://www.bmj.com/content/348/bmj.g1996\">Weich et al.</a> that largely replicated our results in an even better-done study of a larger group, and <a href=\"http://www.europeanneuropsychopharmacology.com/article/S0924-977X(15)00224-2/abstract\">Palmaro et al.</a> that clarified how short-term hypnotics exposures have particularly high risk ratios.  New British and Taiwan studies have expanded the evidence for severe infection risks of hypnotics, a phenomenon that requires much more clinical and laboratory exploration.&#8221;</em></p>\n<p>Also of note is the extensive <a href=\"http://bmjopen.bmj.com/content/2/1/e000850.full#responses\">post-publication discussion</a>, along with <a href=\"http://www.altmetric.com/details/624093/twitter\">extensive mentioning on Twitter</a> that continues four years later.</p>\n<p>Dr Kripke notes,</p>\n<p><em>&#8220;In BMJ Open, the lively COMMENTS section response was very helpful in clarifying scientific critiques of our work, some of which I had the opportunity to rebut, and in disclosing the public emotional reactions that we need to understand for public health interventions.&#8221;</em><!--TrendMD v2.4.6--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"35fce28c-2473-584b-99dd-d8cce8eb549e"}}

